.usa-grid-container {
  margin-left: auto;
  margin-right: auto;
  max-width: 64rem;
  padding-left: 1rem;
  padding-right: 1rem;
}
@media all and (min-width: 64em) {
  .usa-grid-container {
    padding-left: 2rem;
    padding-right: 2rem;
  }
}

.usa-grid-row {
  display: flex  !important;
  flex-wrap: wrap  !important;
  flex-direction: column;
  align-items: stretch;
}
@media all and (min-width: 40em) {
  .usa-grid-row {
    margin-left: -0.5rem;
    margin-right: -0.5rem;
    flex-direction: row;
  }
  .usa-grid-row > * {
    padding-left: 0.5rem;
    padding-right: 0.5rem;
  }
}

.usa-grid-col {
  flex: 0 0 auto;
  width: auto;
  max-width: 100%;
  width: 100%;
}
@media all and (min-width: 40em) {
  .usa-grid-col {
    flex: 0 0 auto;
    width: 50%;
  }
}

.default-container {
  background-color: #fcfcfc;
}

html,
body {
  font-family: Public Sans Web, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol;
  font-size: 1rem;
  line-height: 1.6;
  height: auto;
  color: rgba(0, 0, 0, 0.8);
}

.usa-center {
  text-align: center;
}

.usa-prose site-prose {
  font-family: Public Sans Web, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol;
  font-size: 1rem;
  line-height: 1.6;
}

.sticky {
  position: sticky;
  top: 0;
}
.sticky::before, .sticky::after {
  content: "";
  display: table;
}

.site-header {
  background-color: #252f3e;
}
@media all and (min-width: 64em) {
  .site-header {
    border-bottom: none;
  }
}
.site-header .site-logo {
  margin-left: 1rem;
}
@media all and (min-width: 64em) {
  .site-header .site-logo {
    display: initial;
    float: left;
    margin-bottom: 0;
    margin-left: 2rem;
  }
}
@media all and (min-width: 64em) {
  .site-header .site-logo .usa-logo__text {
    display: initial;
    letter-spacing: -0.01em;
  }
}
.site-header .usa-button-list {
  display: none;
  margin-top: -0.25rem;
}
@media all and (min-width: 64em) {
  .site-header .usa-button-list {
    float: right;
    line-height: 5rem;
    margin-top: 0;
    display: block;
    padding-right: 2rem;
  }
}
.site-header .usa-button-list li {
  display: inline;
  font-family: Public Sans Web, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol;
}
.site-header .usa-button-list li:last-child .usa-button {
  margin-right: 0;
}
.site-header .usa-button-list .usa-button {
  margin-bottom: 0;
  margin-top: 0;
}

@media all and (min-width: 64em) {
  .navbar__search .usa-input {
    background-color: #13171f;
    color: white;
    height: 39px;
  }
}
@media all and (min-width: 64em) {
  .navbar__search .usa-button {
    height: 39px;
  }
}

.site-banner {
  background-color: #1b1b1b;
}
.site-banner .usa-banner__inner {
  max-width: 100%;
}
.site-banner .usa-banner__content {
  color: white;
  margin-left: 0;
}
.site-banner .usa-banner__button {
  color: #adadad;
}
.site-banner .usa-banner__button:hover {
  color: white;
}
.site-banner .usa-banner__header-text {
  color: white;
}
.site-banner .usa-banner__header-action {
  color: #adadad;
}

.site-header-navbar {
  background-color: white;
  border-bottom: none !important;
}
@media all and (min-width: 64em) {
  .site-header-navbar {
    height: 5rem;
    width: 100%;
    display: block;
  }
}
@-moz-document url-prefix() {
  @media all and (min-width: 64em) {
    .site-header-navbar {
      padding-top: 1px;
    }
  }
}

