}
.site-nav-secondary .usa-current::after {
  background-color: #2672de;
  border-radius: 0;
  content: "";
  display: block;
  position: absolute;
  height: 0.25rem;
  left: 1rem;
  right: 1rem;
  bottom: 0;
}
.site-nav-secondary .usa-current:hover {
  color: rgba(0, 0, 0, 0.8);
